#song-list-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: black;
    color: white;
    border-radius: 8px;
    padding: 5px;
}

#song-wrapper {
    display: flex;
    margin-bottom: 0px;
    padding-bottom: 0px;
    align-items: center;
    position: relative;
    height: 22px;
}

#song-wrapper > * {
    margin-top: 0px;
    margin-bottom: 0px;
}

hr {
    width: 100%;
    margin-top: 3px;
    margin-bottom: 0px;
}

#song-wrapper > h5 {
    position: absolute;
    right: 100px;
}

#song-index {
    position: absolute;
    left: 5px;
}

#song-list-title {
    position: absolute;
    left: 35px;
}

#song-list-play {
    position: absolute;
    right: 10px;
}

#song-list-like {
    position: absolute;
    right: 50px;
}

#song-list-add {
    position: absolute;
    right: 100px;
}

#playlist-modal {
    width: 500px;
    z-index: 25;
    position: fixed;
    top: 40%;
    bottom: 60%;
    left: 50%;
    right: 50%;
    background-color: lightgray;
}

#playlist-modal > select {
    width: 150px;
}

#playlist-modal > div {
    background-color: lightgray;
    padding: 5px;
    border-radius: 10px;
    border: solid 2px black;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#playlist-modal > div > button {
    margin-top: 12px;
}
